import React, {Component} from 'react'
/* import PropTypes from 'prop-types' to set default type for props */
import PropTypes from 'prop-types'
import styled from 'styled-components'
import {Link} from 'react-router-dom'
import {ProductConsumer} from '../Context'
export default class Product extends Component{
    render(){
        const {id, title, img, price, inCart} = this.props.product
        return(
            <ProductWrapper className='col-9 mx-auto col-md-6 col-lg-3 my-3'>
              <div className='card'>
                <ProductConsumer>
                  {value =>{
                    return (
                    <div className='img-container p-5' onClick={() => value.handleDetail(id)}>
                    <Link to='/details'>
                      <img src={img} alt='product' className='card-img-top'/>
                    </Link>
                    <button
                       className='cart-btn'
                       disabled={inCart? true:false}
                       onClick={ () => {
                         value.addToCart(id)
                         value.modalOpen(id)
                       }}
                    >
                     {inCart ? <p className='text-capitalize mb-0' disabled>in Cart</p> :
                     <i className='fas fa-cart-plus'/>
                     } 
                    </button>
                  </div>
                    )
                  }}
                </ProductConsumer>
                  {/* Card Footer */}
                  <div
                    className='card-footer d-flex justify-content-between'
                  >
                  <p className='align-self-center mb-0'>
                    {title}
                  </p> 
                  <h5 className='text-blue font-italic mb-0'>
                    <span className='mr-1'>$</span>
                    {price}
                  </h5>
                  </div>
              </div>
            </ProductWrapper>
        )
    }
}
//  Setting Prop Types
Product.propTypes = {
  product: PropTypes.shape({
    id: PropTypes.number,
    img: PropTypes.string,
    title: PropTypes.string,
    price: PropTypes.number,
    company: PropTypes.string,
    info: PropTypes.string,
    inCart: PropTypes.bool,
    count: PropTypes.number,
    total: PropTypes.number
  }).isRequired    
}

const ProductWrapper = styled.div `
.card{
  border-color: transparent;
  tarnsition: all 1.3s linear;
}
.card-footer{
  background: transparent;
  border-top: transparent;
  transition: all 1.3s linear;
}
&:hover{
  .card{
    border: 0.04rem solid rgb(0, 0, 0, .02);
    box-shadow: 2px 2px 5px 0 rgb(0, 0, 0, 0.2)
  }
  .card-footer{
    background: rgb(247, 247, 247)
  }
}
  .img-container{
    position: relative;
    overflow: hidden;
    transition: all 1.3s linear;
  }
  .card-img-top{
    transition: all 1.3s linear;
  }
  .img-container:hover .card-img-top{
    transform: scale(1.2)
  }
.cart-btn{
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 0.2rem 0.4rem;
    background: var(--lightBlue);
    border: none;
    color: var(--mainWhite);
    font-size: 1.4rem;
    border-radius: 0.5rem 0 0 0;
    transform: translate(100%, 100%);
    transition: all 1.3s linear;
  }
  .img-container:hover .cart-btn{
    transform: translate(0, 0);
  }
  .cart-btn:hover{
    color: var(--mainBlue);
    cursor: pointer;
  }
  .cart-btn:focus{
    outline: none;
  }
`